<template>
  <div id="areaManagement-wrapper">
    <div class="statistical">
      <transition name="fade">
        <div class="sta_box" :class="{activeBox: boxstate === 1}" style="left:0;top:0;padding: 15px 5px 5px 15px">
          <div class="con_box">
            <Button :icon="boxstate === 1 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 1 ? 0 :1" size="small" class="boxBtn"></Button>
            <div class="bigBox" :class="{smallToBig:boxstate === 1}">
              <div class="smallBox">
                <BarChart1 :data="getBarData1"></BarChart1>
              </div>
            </div>
            <Spin size="large" fix v-if="spinShow1"></Spin>
          </div>
        </div>
      </transition>
      <div class="sta_box" :class="{activeBox: boxstate === 2}" style="left:50%;top:0;padding:15px 15px 5px 5px">
        <div class="con_box">
          <Button :icon="boxstate === 2 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 2 ? 0 :2" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 2}">
            <div class="smallBox">
              <PieChart1 :data="getPieData1"></PieChart1>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow2"></Spin>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 3}" style="left:0;top:50%;padding: 5px 5px 15px 15px">
        <div class="con_box">
          <Button :icon="boxstate === 3 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 3 ? 0 :3" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 3}">
            <div class="smallBox">
              <BarChart2 :data="getBarData2"></BarChart2>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow3"></Spin>
        </div>
      </div>
      <div class="sta_box" :class="{activeBox: boxstate === 4}" style="left:50%;top:50%;padding: 5px 15px 15px 5px">
        <div class="con_box">
          <Button :icon="boxstate === 4 ? 'md-contract' : 'md-expand'" @click="boxstate = boxstate === 4 ? 0 :4" size="small" class="boxBtn"></Button>
          <div class="bigBox" :class="{smallToBig:boxstate === 4}">
            <div class="smallBox">
              <PieChart2 :data="getPieData2"></PieChart2>
            </div>
          </div>
          <Spin size="large" fix v-if="spinShow4"></Spin>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BarChart1 from './BarChart1.vue'
import BarChart2 from './BarChart2.vue'
import PieChart1 from './PieChart1.vue'
import PieChart2 from './PieChart2.vue'
import {
  // 按类型统计数量数据
  getCountGroupByType,
  // 按类型统计面积数据
  getAreaGroupByType,
  getAreaChange,
  getCountChange
} from '@/api/reportAnalysis'
export default {
  name: 'dataAnalysis',
  data () {
    return {
      /* 放大块的标识 */
      boxstate: 0,
      // 柱状图数据
      barChartData: {},
      // 统计图数据
      getBarData1: [],
      getPieData1: [],
      getBarData2: [],
      getPieData2: [],
      // 统计图数据加载状态
      spinShow1: true,
      spinShow2: true,
      spinShow3: true,
      spinShow4: true
    }
  },
  components: {
    BarChart1,
    BarChart2,
    PieChart1,
    PieChart2
  },
  created () {
    this.getTableData1()
    this.getTableData2()
    this.getTableData3()
    this.getTableData4()
  },
  methods: {
    /* 获取按类型统计数量数据 */
    getTableData1 () {
      getCountGroupByType({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow1 = false
          // 第一个柱状统计图
          this.getBarData1 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow1 = false
              this.getBarData1.push({
                type: item.type,
                count: item.count
              })
            })
          } else {
            this.spinShow1 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取按类型统计面积数据 */
    getTableData2 () {
      getAreaGroupByType({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow1 = false
          // 第二个饼状统计图
          this.getPieData1 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow2 = false
              this.getPieData1.push({
                type: item.type,
                area: item.area
              })
            })
          } else {
            this.spinShow2 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取按类型统计数量数据 */
    getTableData3 () {
      getCountChange({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow3 = false
          // 第三个柱状统计图
          this.getBarData2 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow3 = false
              this.getBarData2.push({
                type: item.name,
                count: item.count
              })
            })
          } else {
            this.spinShow3 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取按类型统计面积数据 */
    getTableData4 () {
      getAreaChange({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.spinShow4 = false
          // 第二个饼状统计图
          this.getPieData2 = []
          if (res.data.data.length > 0) {
            res.data.data.forEach((item, index) => {
              // 统计图loading
              this.spinShow4 = false
              this.getPieData2.push({
                type: item.name,
                area: item.area
              })
            })
          } else {
            this.spinShow4 = false
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #areaManagement-wrapper{
    .statistical{
      position: relative;
      width: 100%;
      height:100% ;
      .sta_box{
        position: absolute;
        height: 50%;
        width: 50%;
        padding:15px 20px;
        .con_box{
          height: 100%;
          width: 100%;
          background: #fff;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          overflow: hidden;
          .boxBtn{
            position: absolute;
            right: 10px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:calc(100vw - 60px);
            height:100%;
            .smallBox{
              width:50%;
              height:100%;
              float: left;
            }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
      /* box放大 */
      .activeBox{
        left:0!important;
        top:0!important;
        padding: 15px !important;
        width: 100% !important;
        height: 100%;
        z-index: 99;
        transition: all 0.8s ease;
      }
    }
  }
</style>
